import { Injectable, Inject, isDevMode } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';

import { Observable, of, BehaviorSubject } from 'rxjs';
import { catchError, map } from 'rxjs/operators';

import { StompRService, StompState } from '@stomp/ng2-stompjs';
import { Message } from '@stomp/stompjs';

import { LoggerOperationResult, TBServerInfo } from '../models/logger-operation-result.model';
import { EntriesParams } from '../models/entries.model';
import { MonitorParams } from '../models/monitor.model';
import { TbLoggerEnvironment } from '../models/logger-environment';

let loggerInstance: TbLoggerService;
export const logger = () => loggerInstance;

export interface LogEntry {
    Message: string;
    Registeredappid: string;
    AccountName?: string | null;
    Subscription?: string | null;
    App?: string;
    Category?: string;
    Level: LogLevel;
}

export enum LogLevel {
    Trace = 0,
    Debug,
    Warn,
    Error
}

export function prepareLog(message: string, logLevel: LogLevel = LogLevel.Debug) {
    const log: LogEntry = {
        Message: message,
        Registeredappid: logger().getAppId(),
        AccountName: localStorage.getItem('_accountname'),
        Subscription: localStorage.getItem('_company'),
        Category: 'Client',
        Level: logLevel
    };
    return log;
}

@Injectable({
    providedIn: 'root'
})
export class TbLoggerService {
    loggerUrl: string;
    serverMonitorUrl: string;
    private howMany = 100;

    mqConnectionState: StompState = StompState.CLOSED;
    mqConnectionStateObservable: BehaviorSubject<number> = new BehaviorSubject(StompState.CLOSED);

    constructor(@Inject('env') private env: TbLoggerEnvironment, private http: HttpClient, public stompService: StompRService) {
        loggerInstance = this;
        this.loggerUrl = this.env.logger.url;
        this.serverMonitorUrl = this.env.serverMonitor.url;
        if (env.stompConfig) this.mqInit();
    }

    mqInit() {
        if (this.env.stompConfig) {
            this.stompService.config = this.env.stompConfig;
            this.stompService.initAndConnect();
            this.stompService.state.subscribe((status: StompState) => {
                this.mqConnectionState = status;
                this.mqConnectionStateObservable.next(status);
            });
        }
    }

    /**
     * Ritorna la App Id dell'applicazione frontend che sta loggando,
     * caricata da un file di configurazione caricato dinamicamente (assets/environment.json)
     */
    getAppId() {
        return this.env.logger.appId;
    }

    /**
     * Ritorna la base url del logger,
     * caricata da un file di configurazione caricato dinamicamente (assets/environment.json)
     */
    getLoggerUrl() {
        return this.loggerUrl ? this.loggerUrl : this.env.logger.url;
    }

    /**
     * Ritorna la api per inserire log con appId configurato in environment.json
     */
    getLoggerPostUrl() {
        return `${this.getLoggerUrl()}entries/${this.getAppId()}/`;
    }

    /**
     * M4 Backend URL
     */
    getServerMonitorUrl() {
        return this.serverMonitorUrl ? this.serverMonitorUrl : this.env.serverMonitor.url;
    }

    private _shouldLog = (logLevel: LogLevel) => logLevel >= this.env.logger.level;
    private _serverLog = (logLevel: LogLevel, message: string) => this._shouldLog(logLevel) && this.sendLog(message, logLevel);

    private sendLog(message: string, logLevel: LogLevel) {
        this.http
            .post(this.getLoggerPostUrl(), prepareLog(message, logLevel))
            .toPromise()
            .then(__ => {}, err => {});
    }

    /**
     * @param message
     * @param optionalParams
     */
    log(message?: any): void {
        if (this._shouldLog(LogLevel.Trace)) console.log(`%c${message}`, `color:#3daf67`);
        this._serverLog(LogLevel.Trace, message);
    }

    /**
     * @param message
     * @param optionalParams
     */
    debug(message?: any): void {
        if (this._shouldLog(LogLevel.Debug)) console.log(`%c${message}`, `color:#0277bd`);
        this._serverLog(LogLevel.Debug, message);
    }

    /**
     * @param message
     * @param optionalParams
     */
    warn(message?: any): void {
        if (this._shouldLog(LogLevel.Warn)) console.log(`%c${message}`, `color:#FF9633`);
        this._serverLog(LogLevel.Warn, message);
    }

    /**
     * @param message
     * @param optionalParams
     */
    error(message?: any): void {
        if (this._shouldLog(LogLevel.Error)) console.error(`%c${message}`, `color:red`);
        this._serverLog(LogLevel.Error, message);
    }

    /**
     * Log per statistiche client (es: usato da login page per tracciare risoluzione schermo)
     *
     * @param message
     * @param optionalParams
     */
    stat(message?: any): void {
        console.log(`%c${message}`, `color:#884EA0`);
        this._serverLog(LogLevel.Trace, message);
    }

    /**
     * Return logs: LoggerOperationResult
     *
     * @param params: EntriesParams
     */
    public getLogs(params: EntriesParams): Observable<LoggerOperationResult> {
        if (!params.instanceKey) {
            const error = new LoggerOperationResult(false, 'Error - No instanceKey, no party');
            return of(error);
        }
        // console.log('this.getLoggerUrl()', this.getLoggerUrl());
        const url = this.getLoggerUrl() + `entries/${params.instanceKey}`;

        let p = new HttpParams();
        p = p.append('howMany', '' + this.howMany);
        if (params.accountName) p = p.append('accountName', params.accountName);
        if (params.subscriptionKey) p = p.append('subscription', params.subscriptionKey);
        if (params.category) p = p.append('categories', params.category);
        if (params.apps) p = p.append('apps', params.apps);
        if (params.appTypes) p = p.append('appTypes', params.appTypes);
        if (params.levels) p = p.append('levels', params.levels);
        if (params.rangeDateStart && params.rangeDateEnd) {
            if (params.rangeDateStart === params.rangeDateEnd) p = p.append('date', params.rangeDateStart);
            else p = p.append('date', params.rangeDateStart + ';' + params.rangeDateEnd);
        }
        const httpOptions = {
            params: p
        };

        return this.http.get<LoggerOperationResult>(url, httpOptions).pipe(
            // tap(op => console.log('TbLoggerService.getLogs', op)),
            catchError(this.handleError('TbLoggerService.getLogs', new LoggerOperationResult(false, 'Error - getLogs')))
        );
    }

    public getApps(appId: string): Observable<LoggerOperationResult> {
        if (!appId) {
            const error = new LoggerOperationResult(false, 'Error - No appId, no party');
            return of(error);
        }

        const url = this.getLoggerUrl() + `apps/${appId}`;

        return this.http.get<LoggerOperationResult>(url).pipe(
            // tap(op => console.log('TbLoggerService.getApps with appId: ', appId, op)),
            catchError(this.handleError('TbLoggerService.getApps', new LoggerOperationResult(false, 'Error - getApps')))
        );
    }

    public getAppTypes(appId: string): Observable<LoggerOperationResult> {
        if (!appId) {
            const error = new LoggerOperationResult(false, 'Error - No appId, no party');
            return of(error);
        }

        const url = this.getLoggerUrl() + `appTypes/${appId}`;

        return this.http.get<LoggerOperationResult>(url).pipe(
            // tap(op => console.log('TbLoggerService.getAppTypes with appId: ', appId, op)),
            catchError(this.handleError('TbLoggerService.getAppTypes', new LoggerOperationResult(false, 'Error - getAppTypes')))
        );
    }

    public getInstanceKey(): Observable<LoggerOperationResult> {
        const url = this.getLoggerUrl() + `appIds`;

        return this.http.get<LoggerOperationResult>(url).pipe(
            // tap(op => console.log(url, 'TbLoggerService.getInstanceKey with appId: ', op)),
            catchError(this.handleError('TbLoggerService.getInstanceKey', new LoggerOperationResult(false, 'Error - getInstanceKey')))
        );
    }

    /**
     * Legge tutte le subscription
     */
    public getSubscriptionKey(): Observable<LoggerOperationResult> {
        const url = this.getLoggerUrl() + `subscriptionKeys`;

        return this.http.get<LoggerOperationResult>(url).pipe(
            // tap(op => console.log('TbLoggerService.getSubscriptionKey: ', op)),
            catchError(
                this.handleError('TbLoggerService.getSubscriptionKey', new LoggerOperationResult(false, 'Error - getSubscriptionKey'))
            )
        );
    }

    public getCategories(appId: string): Observable<LoggerOperationResult> {
        if (!appId) {
            const error = new LoggerOperationResult(false, 'Error - No appId, no party');
            return of(error);
        }

        const url = this.getLoggerUrl() + `categories/${appId}`;

        return this.http.get<LoggerOperationResult>(url).pipe(
            // tap(op => console.log('TbLoggerService.getCategories with appId: ', appId, op)),
            catchError(this.handleError('TbLoggerService.getCategories', new LoggerOperationResult(false, 'Error -  getCategories')))
        );
    }

    /**
     * Handle Http operation that failed.
     * Let the app continue.
     * @param operation - name of the operation that failed
     * @param result - optional value to return as the observable result
     */
    public handleError<T>(operation = 'operation', result?: T) {
        return (error: any): Observable<T> => {
            console.error(`${operation} failed: ${error.error}`);

            // Let the app keep running by returning an empty result.
            return of(result as T);
        };
    }

    /**
     * Connessione a RabbitMQ
     */
    public mqConnect(queueName: string) {
        return this.stompService.subscribe(queueName).pipe(map((msg: Message) => JSON.parse(msg.body)));
    }

    public mqConnected() {
        return this.stompService.connected();
    }
    public mqDisconnect() {
        return this.stompService.disconnect();
    }

    // -------------------------------------------------------------------------------------------------------
    // PARTE TBSERVERMONITOR
    // -------------------------------------------------------------------------------------------------------
    public getTBInfosLogs(params: MonitorParams): Observable<boolean | TBServerInfo[]> {
        let p = new HttpParams();

        if (params.accountName) p = p.append('accountName', '' + params.accountName);
        if (params.processName) p = p.append('processName', '' + params.processName);
        if (params.minutes) p = p.append('minute', '' + params.minutes);

        const httpOptions = { params: p };

        const url = this.getServerMonitorUrl() + `tbServers/${params.instanceKey}`;

        return this.http.get<TBServerInfo[]>(url, httpOptions).pipe(catchError(this.handleError('TbLoggerService.getTBInfosLogs', false)));
    }
}
